html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0 0 0.67em;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

:root {
  --color-black: #000;
  --color-white: #fff;
  --color-grey: #777;
  --color-dark-grey: #333;
  --color-blue: #235ef5;
  --color-blue-hover: #396ef6;
  --color-red: #ff4b55;
  --color-bg: #f1f1f1;
  --color-border: #ccc;
  --border-radius: 4px;
}

html {
  font-size: 16px;
  line-height: 1.4;
  font-weight: 400;
  height: 100%;
}

body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: var(--color-dark-grey);
  min-height: 100%;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  color: inherit;
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header {
  background-color: var(--color-bg);
  border-bottom: 1px solid var(--color-border);
  position: relative;
}

@media (max-width: 767px) {
  .header__search {
    order: -1;
    margin-bottom: 10px;
  }
}
@media (min-width: 768px) {
  .header__search {
    margin: 0 10px;
  }
}

.nav {
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .nav {
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 1440px) {
  .nav {
    padding-left: 50px;
    padding-right: 50px;
  }
}
@media (max-width: 767px) {
  .nav--active .nav__header {
    margin: 0 -15px;
    padding: 0 15px;
  }
  .nav--active .nav__toggle-item {
    height: 0;
  }
  .nav--active .nav__toggle-item::before {
    transform: translate(0) rotate(45deg);
  }
  .nav--active .nav__toggle-item::after {
    transform: translate(0) rotate(-45deg);
  }
  .nav--active .nav__content {
    transform: translateX(0);
    z-index: 9;
  }
}
@media (min-width: 768px) {
  .nav {
    display: flex;
    align-items: center;
    padding-top: 5px;
    padding-bottom: 5px;
  }
}

@media (max-width: 767px) {
  .nav__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
@media (min-width: 768px) {
  .nav__header {
    margin-right: 10px;
  }
}

.nav__logo {
  display: block;
  font-size: 18px;
  line-height: 1;
  padding: 15px;
  margin-left: -15px;
}

.nav__toggle {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 24px;
  height: 24px;
  padding: 0;
  background-color: transparent;
  border: none;
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}

.nav__toggle-item {
  width: 20px;
  height: 2px;
  background-color: currentColor;
  position: relative;
}
.nav__toggle-item::before, .nav__toggle-item::after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: inherit;
  position: absolute;
  top: 0;
  left: 0;
  transition: transform 0.2s;
}
.nav__toggle-item::before {
  transform: translateY(-6px);
}
.nav__toggle-item::after {
  transform: translateY(6px);
}

@media (max-width: 767px) {
  .nav__content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    font-size: 22px;
    font-weight: 700;
    text-align: center;
    width: 100vw;
    height: calc(100vh - 49px);
    background-color: var(--color-bg);
    position: absolute;
    top: calc(100% + 1px);
    left: 0;
    right: 0;
    transform: translateX(-100%);
    transition: 0.3s ease-in;
  }
}
@media (min-width: 768px) {
  .nav__content {
    display: flex;
    align-items: center;
    width: 100%;
  }
}

.nav__list {
  list-style: none;
  margin: 0;
  padding-left: 0;
}
@media (min-width: 768px) {
  .nav__list {
    display: flex;
    align-items: center;
  }
  .nav__list--left {
    margin-right: auto;
  }
  .nav__list--right {
    margin-left: auto;
  }
}

.nav__link {
  display: block;
  color: var(--color-grey);
  white-space: nowrap;
  padding-top: 10px;
  padding-bottom: 10px;
  transition: 0.2s;
}
.nav__link:hover, .nav__link:focus-visible {
  color: var(--color-dark-grey);
}
@media (min-width: 768px) {
  .nav__link {
    padding: 10px;
  }
}

.main__container {
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  box-sizing: border-box;
  height: 100%;
  padding-top: 50px;
  padding-bottom: 50px;
}
@media (min-width: 768px) {
  .main__container {
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 1440px) {
  .main__container {
    padding-left: 50px;
    padding-right: 50px;
  }
}

.main__title {
  text-align: center;
}

.auth {
  width: 100%;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
}

.login__help {
  margin-top: 15px;
}

.login__help-text:last-child {
  margin-bottom: 0;
}
.login__help-text > a {
  color: var(--color-grey);
  text-decoration: underline;
  transition: 0.2s;
}
.login__help-text > a:hover {
  color: var(--color-blue);
}

.alert {
  color: #004085;
  margin-bottom: 30px;
  padding: 15px;
  background-color: #cce5ff;
  border-color: #b8daff;
}
.alert--info {
  color: #0c5460;
  background-color: #d1ecf1;
  border-color: #bee5eb;
}
.alert--success {
  color: #155724;
  background-color: #d4edda;
  border-color: #c3e6cb;
}
.alert--warning {
  color: #856404;
  background-color: #fff3cd;
  border-color: #ffeeba;
}
.alert--danger {
  color: #721c24;
  background-color: #f8d7da;
  border-color: #f5c6cb;
}

.form__group {
  margin-bottom: 15px;
}
.form__group:last-child {
  margin-bottom: 0;
}
.form__group--checkbox {
  margin-top: 15px;
}
.form__group--checkbox .form__label {
  font-weight: 400;
}

.form__input {
  display: block;
  width: 100%;
  padding: 10px 15px;
  background-color: #fff;
  background-image: none;
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius);
  box-sizing: border-box;
  transition: 0.2s;
}

.form__label {
  display: inline-block;
  max-width: 100%;
  font-weight: 700;
  margin-bottom: 5px;
  cursor: pointer;
}
.form__label:last-child {
  margin-bottom: 0;
}

.form__submit {
  color: var(--color-white);
  font-weight: 700;
  width: 100%;
  padding: 12px 16px;
  background-color: var(--color-blue);
  border: none;
  border-radius: var(--border-radius);
  cursor: pointer;
  transition: 0.2s;
}
.form__submit:hover, .form__submit:focus-visible {
  background-color: var(--color-blue-hover);
}

.footer {
  padding-top: 15px;
  padding-bottom: 15px;
  text-align: center;
  background-color: var(--color-bg);
  border-top: 1px solid var(--color-border);
  font-size: 18px;
  line-height: 1;
}